import React, { useState, useRef, useEffect } from 'react';
// import DatePicker from "react-multi-date-picker"
import DatePicker from "./components/date_picker/date_picker"
import Example from "./components/example"
import { ReactComponent as GitHub } from "./assets/github.svg"
import { ReactComponent as NPM } from "./assets/npm.svg"
import screenshot from "./assets/screenshot.jpg"
import Calendar from './components/calendar/calendar';
import "./calendar_demo.css"

function App() {
  const [isActive, setIsActive] = useState(false)
  const sidebarRef = useRef(null)
  const [calendar, setCalendar] = useState("gregorian")
  const [local, setLocal] = useState("en")
  const [range, setRange] = useState(false)
  const [date, setDate] = useState(new Date())
  const [timePicker, setTimePicker] = useState(false)
  const [onlyTimePicker, setOnlyTimePicker] = useState(false)
  const [mustShowDates, setMustShowDates] = useState(true)

  useEffect(() => {
    document.addEventListener("click", handleClickOutside, false)

    return () => document.removeEventListener("click", handleClickOutside, false)

    function handleClickOutside(event) {
      if (sidebarRef.current && !sidebarRef.current.contains(event.target) && !event.target.className.includes("open-sidebar")) {
        setIsActive(false);
      }
    }
  }, [])

  const logos = {
    npm: <NPM />,
    github: <GitHub />
  }

  let Doc = {
    title: "DatePicker",
    screenshot,
    navbar: {
      title: "React multi Date Picker",
      logos: [
        { name: "github", url: "https://github.com/shahabyazdi/react-multi-date-picker" },
        { name: "npm", url: "https://www.npmjs.com/package/react-multi-date-picker" }
      ]
    },
    examples: [{
      title: "Installation",
      code: "npm install --save react-multi-date-picker"
    }, {
      title: "Usage",
      code: `import react from "react";
  import DatePicker from "react-multi-date-picker";

  export default function Example() {
    return <DatePicker />
  }`
    }, {
      title: "Example",
      examples: [{
        title: "01- Date #1",
        code: `<DatePicker
    value={new Date()}
  />`,
        jsx: <DatePicker
          value={new Date()}
        />
      }, {
        title: "02- Date #2",
        code: `<DatePicker
    value={new Date(2020, 9, 27)}
  />`,
        jsx: <DatePicker
          value={new Date(2020, 9, 27)}
        />
      }, {
        title: "03- String Date #1",
        code: `<DatePicker
      value="2020/08/27"
  />`,
        jsx: <DatePicker
          value="2020/08/27"
        />
      }, {
        title: "04- String Date #2",
        code: `<DatePicker
    value="August 27 2020"
    format="MMMM DD YYYY"
  />`,
        jsx: <DatePicker
          value="August 27 2020"
          format="MMMM DD YYYY"
        />
      }, {
        title: "05- timePicker #1",
        code: `<DatePicker
    value={new Date()}
    format="YYYY-MM-DD HH:mm:ss"
    timePicker
  />`,
        jsx: <DatePicker
          value={new Date()}
          format="YYYY-MM-DD HH:mm:ss"
          timePicker
        />
      }, {
        title: "06- timePicker #2",
        code: `<DatePicker
    value="08/27/2020 12:30:31"
    format="MM/DD/YYYY HH:mm:ss"
    timePicker
  />`,
        jsx: <DatePicker
          value="08/27/2020 12:30:31"
          format="MM/DD/YYYY HH:mm:ss"
          timePicker
        />
      }, {
        title: "07- onlyTimePicker #1",
        code: `<DatePicker
    value={new Date()}
    format="hh-mm-ss A"
    onlyTimePicker
  />`,
        jsx: <DatePicker
          value={new Date()}
          format="hh-mm-ss A"
          onlyTimePicker
        />
      }, {
        title: "08- onlyTimePicker #2",
        code: `<DatePicker
    value="12:30:31 am"
    format="hh:mm:ss a"
    onlyTimePicker
  />`,
        jsx: <DatePicker
          value="12:30:31 am"
          format="hh:mm:ss a"
          onlyTimePicker
        />
      }, {
        title: "09- gregorian",
        code: `<DatePicker
    value="1399/06/05 12:30:24 pm"
    format="YYYY/MM/DD hh:mm:ss a"
    calendar="gregorian"
    local="en"
    timePicker
  />`,
        jsx: <DatePicker
          value="2020/09/01 12:30:24 pm"
          format="YYYY/MM/DD hh:mm:ss a"
          calendar="gregorian"
          local="en"
          timePicker
        />
      }, {
        title: "10- persian",
        code: `<DatePicker
    value={new Date()}
    format="HH:mm:ss YYYY/MM/DD"
    calendar="persian"
    local="fa"
    timePicker
  />`,
        jsx: <DatePicker
          value={new Date()}
          format="HH:mm:ss YYYY/MM/DD"
          calendar="persian"
          local="fa"
          timePicker
        />
      }, {
        title: "10- arabic",
        code: `<DatePicker
    value={new Date()}
    format="HH:mm:ss YYYY/MM/DD"
    calendar="arabic"
    local="ar"
    timePicker
  />`,
        jsx: <DatePicker
          value={new Date()}
          format="HH:mm:ss YYYY/MM/DD"
          calendar="arabic"
          local="ar"
          timePicker
        />
      }, {
        title: "11- liveChange #1",
        code: `<DatePicker
          value="2020/08/27 11:30:24 pm"
          format="YYYY/MM/DD hh:mm:ss a"
          calendar="gregorian"
          local="en"
          timePicker
          liveChange
        />`,
        jsx: <DatePicker
          value="2020/08/27 11:30:24 pm"
          format="YYYY/MM/DD hh:mm:ss a"
          calendar="gregorian"
          local="en"
          timePicker
          liveChange
        />
      }, {
        title: "12- liveChange #2",
        code: `<DatePicker
    value={new Date()}
    format="hh-mm-ss.SSS A"
    liveChange
    onlyTimePicker
  />`,
        jsx: <DatePicker
          value={new Date()}
          format="hh-mm-ss.SSS A"
          liveChange
          onlyTimePicker
        />
      }]
    }, {
      title: "Props",
      code: "",
      jsx: <table>
        <thead>
          <tr>
            <th>Key</th>
            <th>Value</th>
            <th>Default</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>date</td>
            <td>Date,<a target="_blank" rel="noopener noreferrer" href="https://github.com/shahabyazdi/date-object">DateObject</a>, String or Number </td>
            <td>new Date()</td>
          </tr>
          <tr>
            <td>timePicker</td>
            <td>Boolean </td>
            <td>false</td>
          </tr>
          <tr>
            <td>onlyTimePicker</td>
            <td>Boolean </td>
            <td>false</td>
          </tr>
          <tr>
            <td>format</td>
            <td>String </td>
            <td>YYYY/MM/DD</td>
          </tr>
          <tr>
            <td>local</td>
            <td>String (gregorian or persian) </td>
            <td>en</td>
          </tr>
          <tr>
            <td>calendar</td>
            <td>String (en or fa)</td>
            <td>en</td>
          </tr>
          <tr>
            <td>liveChange</td>
            <td>Boolean</td>
            <td>false</td>
          </tr>
          <tr>
            <td>onChange</td>
            <td>Function</td>
            <td>undefined</td>
          </tr>
        </tbody>
      </table>
    }]
  }

  function getExamples(examples, count = 0) {
    if (!examples) return null

    return examples.map((example, index) => {
      return (
        <div key={count + index}>
          <Example
            id={"example" + count + "-" + index}
            title={example.title}
            code={example.code}
            jsx={example.jsx}
          />
          {getExamples(example.examples, count + 1)}
        </div>
      )
    })
  }

  function getSidebarItem(examples, count = 0) {
    if (!examples) return null

    return examples.map((example, index) => {
      return (
        <li key={count + index}>
          <a href={"#example" + count + "-" + index}>{example.title}</a>
          <ul className="sidebar-sub-menu">
            {getSidebarItem(example.examples, count + 1)}
          </ul>
        </li>
      )
    })
  }

  return (
    <div className="App">
      <nav className="navbar">
        <span className="open-sidebar" onClick={toggleSidebar}>&#9776;</span>
        <span className="title">{Doc.navbar.title}</span>
        <div className="nav-images">
          {Doc.navbar.logos.map((logo, index) => <div key={index} className="nav-image">
            <a target="_blank" rel="noopener noreferrer" className={logo.name} href={logo.url}>{logos[logo.name]}</a>
          </div>)}
        </div>
      </nav>
      <ul ref={sidebarRef} className={`sidebar ${isActive ? "active" : ""}`}>
        {getSidebarItem(Doc.examples)}
      </ul>
      <div className="main">
        <div className="calendar-demo">
          <Calendar
            value={date}
            calendar={calendar}
            local={local}
            range={range}
            onChange={setDate}
            timePicker={timePicker}
            onlyTimePicker={onlyTimePicker}
            mustShowDates={mustShowDates}
          />
        </div>
        <div className="selector-wrapper">
          <div className="selector-container">
            <span>Calendar</span>
            <div className="selector">
              <div className={`${calendar === "gregorian" ? "active" : ""}`} onClick={() => setCalendar("gregorian")}>Gregorian</div>
              <div className={`${calendar === "persian" ? "active" : ""}`} onClick={() => setCalendar("persian")}>Persian</div>
              <div className={`${calendar === "arabic" ? "active" : ""}`} onClick={() => setCalendar("arabic")}>Arabic</div>
            </div>
          </div>
        </div>
        <div className="selector-wrapper">
          <div className="selector-container">
            <span>Local</span>
            <div className="selector">
              <div className={`${local === "en" ? "active" : ""}`} onClick={() => setLocal("en")}>English</div>
              <div className={`${local === "fa" ? "active" : ""}`} onClick={() => setLocal("fa")}>Persian</div>
              <div className={`${local === "ar" ? "active" : ""}`} onClick={() => setLocal("ar")}>Arabic</div>
            </div>
          </div>
        </div>
        <div className="selector-wrapper">
          <div className="selector-container">
            <span>Type</span>
            <div className="selector">
              <div
                className={`${!Array.isArray(date) ? "active" : ""}`}
                onClick={() => {
                  setDate(Array.isArray(date) ? date[0] : date)
                  setRange(false)
                }}
              >Single</div>
              <div
                className={`${Array.isArray(date) && !range ? "active" : ""}`}
                onClick={() => {
                  if (!Array.isArray(date)) setDate([date])
                  if (range) setRange(false)
                }}
              >Multiple</div>
              <div className={`${range ? "active" : ""}`} onClick={() => {
                if (!Array.isArray(date)) setDate([date])
                setRange(true)
              }}>Range</div>
            </div>
          </div>
        </div>
        {!Array.isArray(date) ? <div className="selector-wrapper">
          <div className="selector-container">
            <span>Time Picker</span>
            <div className="selector">
              <div className={`${!timePicker && !onlyTimePicker ? "active" : ""}`} onClick={() => {
                setTimePicker(false)
                setOnlyTimePicker(false)
              }}>Disable</div>
              <div className={`${timePicker ? "active" : ""}`} onClick={() => {
                setTimePicker(true)
                setOnlyTimePicker(false)
              }}>Enable</div>
              <div className={`${onlyTimePicker ? "active" : ""}`} onClick={() => {
                setOnlyTimePicker(true)
                setTimePicker(false)
              }}>Only TimePicker</div>
            </div>
          </div>
        </div> :
          <div className="selector-wrapper">
            <div className="selector-container">
              <span>Dates Panel</span>
              <div className="selector">
                <div className={`${mustShowDates ? "active" : ""}`} onClick={() => setMustShowDates(true)}>Enable</div>
                <div className={`${!mustShowDates ? "active" : ""}`} onClick={() => setMustShowDates(false)}>Disable</div>
              </div>
            </div>
          </div>
        }
        {getExamples(Doc.examples)}
      </div>
    </div >
  );

  function toggleSidebar() {
    setIsActive(!isActive)
  }
}

export default App;
